import React, { Component, Suspense } from "react";
/* import Chart from "./Chart";
import Part1 from "./part1"; */
import styles from "./slides.css";

const Part1 = React.lazy(() => import("./part1"));
const Chart = React.lazy(() => import("./Chart"));

class Home extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="">
        <Suspense fallback={<div>Loading...</div>}>
          {" "}
          <Part1 />
        </Suspense>

        <br />
        <Suspense fallback={<div>Loading...</div>}>
          <Chart />{" "}
        </Suspense>

        <br />
        <footer class="container" style={{ backgroundColor: "lightgrey" }}>
          <p class="float-end" style={{ textAlign: "right" }}>
            <a href="#" style={{ color: "black" }}>
              Back to top
            </a>
          </p>
          <p
            style={{
              textAlign: "center",
              fontFamily: "cursive",
              fontSize: "20px",
            }}
          >
            Thank you for visiting our store
          </p>
          <p>&copy; 2017â€“2021 Company, Inc.</p>
        </footer>
        <br />
      </div>
    );
  }
}
export default Home;
